'use strict';
var MainTemplate = '<section class="content"><h2>{{$t($route.params.head.label)}}</h2><p>{{$t($route.params.head.description)}}</p><template v-for="sample in $route.params.samples"><h3>{{$t(sample.label)}}</h3><p>{{$t(sample.description)}}</p><div class="demo-block" :class="{\'no-bottom\': sample.notshowmeta!==true}" v-if="sample.notshowblock!==true"><component :is="sample.id"></component></div><vue-collapse v-if="sample.notshowmeta!==true" v-model="sample.collapse" :expand-on-click-header="false"><vue-collapse-item class="collapse-item-blue" name="item1"><template slot="title"><vue-button type="text" icon="vue-icon-document" @click="clickHandle(sample)">{{$t("main.code")}}</vue-button><vue-button v-if="sample.notshowblock!==true" type="text" :style="{float:\'right\',marginRight:\'10px\',lineHeight:\'22px\'}" @click="tryHandle(sample)">Try it on JSRun</vue-button></template><pre class="toolbar:false;brush:jsp" style="padding:0">{{sample.code}}</pre></vue-collapse-item></vue-collapse></template></section>';
var routes = [];
var menuItems1 = [InstallDemo, I18nDemo, AjaxDemo];
var menuItems2 = [LayoutDemo, IconDemo, ButtonDemo, InputDemo, DateDemo, TimeDemo, DateTimeDemo, CalendarDemo, ImageDemo, RadioDemo, CheckboxDemo, SwitchDemo, SelectDemo, CascaderDemo, DropdownDemo, FormDemo, TableDemo, PaginationDemo, TreeDemo, CollapseDemo, SortableDemo, DraggableDemo, SignatureDemo, MessageBoxDemo, DialogDemo, AsideDemo, NotificationDemo, TabsDemo, BadgeDemo, PopoverDemo, TooltipDemo, TagDemo, AlertDemo, NoteDemo, ListDemo, CardDemo, CarouselDemo, LoadingDemo, LoadingBarDemo, ProgressDemo, SliderDemo, BreadcrumbDemo, StepsDemo, RateDemo, PinDemo, UploadDemo, ColorDemo, NavMenuDemo, AdaptiveGroupDemo, RippleDemo, HotkeyDemo];
var demos = [].concat(menuItems1, menuItems2);
var components = {};
var componentsFn = function (componentId, template, parameter) {
    var self = this;
    if (!VueUtil.isDef(template)) return;
    var vueParameter = {
        template: template
    };
    if (parameter) {
        if (parameter.data)
            vueParameter.data = parameter.data;
        if (parameter.methods)
            vueParameter.methods = parameter.methods;
        if (parameter.mounted)
            vueParameter.mounted = parameter.mounted;
        if (parameter.watch)
            vueParameter.watch = parameter.watch;
    }
    if (componentId.indexOf('i18n') !== -1) {
        vueParameter.methods = {
            clickHandle1: function () {
                var self = this;
                self.$root.radioLang = '1';
                self.$nextTick(function () {
                    self.$notify({ title: self.$t('message.hello', { msg: '你好!' }) });
                });
            },
            clickHandle2: function () {
                var self = this;
                self.$root.radioLang = '2';
                self.$nextTick(function () {
                    self.$notify({ title: self.$t('message.hello', { msg: 'こんにちは!' }) });
                });
            }
        }
    }
    components[componentId] = vueParameter;
}
demos.forEach(function (demo) {
    components = {};
    VueUtil.loop(demo.samples, function (demoSample) {
        componentsFn(demoSample.id, demoSample.template, demoSample.parameter);
    });
    routes.push({
        path: demo.path,
        name: demo.name,
        component: {
            template: MainTemplate,
            components: components,
            methods: {
                clickHandle: function (sample) {
                    if (!VueUtil.isDef(sample.collapse)) sample.collapse = [];
                    if (sample.collapse.length > 0) {
                        sample.collapse = [];
                    } else {
                        sample.collapse = ["item1"];
                    }
                    this.$forceUpdate();
                },
                tryHandle: function (sample) {
                    var url = 'http://jsrun.net/QihKp/edit';
                    window.open(url);
                }
            },
            mounted: function () {
                this.$nextTick(function () {
                    SyntaxHighlighter.highlight();
                });
            }
        },
        beforeEnter: function (to, from, next) {
            Vue.loadingBar.start();
            to.params.head = demo.head;
            to.params.samples = demo.samples;
            next();
        }
    });
});
routes.push({
    path: '/changeLog',
    component: ChangeLog,
    beforeEnter: function (to, from, next) {
        Vue.loadingBar.start();
        next();
    }
});
routes.push({
    path: '/',
    component: {
        template: '<div><div class="scene"><div class="scene_titanShadow"></div><div class="t_wrap"><div class="scene_titan"><div class="eyes"><div class="eye eye--left"></div><div class="eye eye--right"></div></div></div></div></div><section class="content root-content"><h2>{{$t(\'main.line1\')}}</h2><p></p><h3 class="imitation">{{txt}}{{h3}}</h3></section></div>',
        data: function () {
            return {
                h3: '',
                txt: '',
                typeInterval: null
            }
        },
        beforeDestroy: function () {
            this.typeInterval && clearInterval(this.typeInterval);
        },
        mounted: function () {
            var self = this;
            var typeFlg = true;
            var msgFlg = 0;
            var typeWriter = {
                msg: function (msg) {
                    return msg;
                },
                len: function () {
                    return this.msg.length;
                },
                seq: 0,
                speed: 400,
                witeTime: 1500,
                backSpeed: 200,
                backFlg: true,
                typeInterval: null,
                type: function () {
                    typeFlg = false;
                    var that = this;
                    that.typeInterval && clearInterval(that.typeInterval);
                    self.h3 = that.msg.substring(0, that.seq);
                    if (that.seq === that.len()) {
                        if (that.backFlg) {
                            setTimeout(function () {
                                that.typeBack()
                            }, that.witeTime);
                        } else {
                            self.txt = self.h3;
                            self.h3 = "";
                            that.seq = 0;
                            typeFlg = true;
                        }
                    } else {
                        that.seq++;
                        setTimeout(function () {
                            that.type()
                        }, that.speed);
                    }
                },
                typeBack: function () {
                    var that = this;
                    self.h3 = that.msg.substring(that.seq, 0);
                    if (that.seq === 0) {
                        typeFlg = true;
                    } else {
                        that.seq--
                        setTimeout(function () {
                            that.typeBack()
                        }, that.backSpeed);
                    }
                }
            }
            var startType = function (msg, speed, backSpeed, backFlg, typeInterval) {
                typeWriter.msg = msg;
                typeWriter.speed = speed;
                typeWriter.backSpeed = backSpeed;
                typeWriter.backFlg = backFlg;
                typeWriter.typeInterval = typeInterval;
                typeWriter.type();
            }
            setTimeout(function () {
                startType(self.$t('main.line21'), 200, 100, true);
                self.typeInterval = setInterval(function () {
                    if (typeFlg) {
                        startType(self.$t('main.line22'), 200, 100, true, self.typeInterval);
                        self.typeInterval = setInterval(function () {
                            if (typeFlg) {
                                startType(self.$t('main.line23'), 200, 0, false, self.typeInterval);
                                self.typeInterval = setInterval(function () {
                                    if (typeFlg) {
                                        switch (msgFlg) {
                                            case 0:
                                                msgFlg++
                                                startType(self.$t('main.line24'), 250, 150, true);
                                                break;
                                            case 1:
                                                msgFlg++
                                                startType(self.$t('main.line25'), 250, 150, true);
                                                break;
                                            case 2:
                                                msgFlg++
                                                startType(self.$t('main.line26'), 250, 150, true);
                                                break;
                                            case 3:
                                                msgFlg++
                                                startType(self.$t('main.line27'), 250, 150, true);
                                                break;
                                            case 4:
                                                msgFlg = 0;
                                                startType(self.$t('main.line28'), 250, 150, true);
                                                break;
                                        }
                                    }
                                }, 1500);
                            }
                        }, 1500);
                    }
                }, 1500);
            }, 1500);
        }
    },
    beforeEnter: function (to, from, next) {
        Vue.loadingBar.start();
        next();
    }
});
routes.push({
    path: '/example',
    redirect: '/'
});
routes.push({
  path: '/redmine',
  redirect: '/'
});
routes.push({
    path: '/@*',
    redirect: '/'
  });
var DemoRouter = new VueRouter({
    routes: routes,
    scrollBehavior: function(to, from, savedPosition) {
      document.getElementsByClassName('right-container')[0].scrollTop = 0;
    }
});
DemoRouter.afterEach(function (to, from) {
    Vue.nextTick(function () {
        Vue.loadingBar.finish();
        vueDemo && vueDemo.filterHandle();
    });
});
var vueDemo = new Vue({
    template: 
    '<div> \
      <vue-menu theme="dark" class="head-container"> \
        <vue-row type="flex" justify="center"> \
          <vue-col :sm=\'2\' :xs=\'2\' class="menu-show"> \
            <vue-button icon="vue-icon-menu" @click="clickHandle" :class="[\'show-menu-btn\',openMenuClass]" \
              type="text"></vue-button> \
          </vue-col> \
          <vue-col :span="10" :offset="1"> \
            <vue-button type="text" class="head-text" @click="headClick">{{headText}}</vue-button> \
          </vue-col> \
          <vue-col :span="11"> \
            <vue-radio-group v-model="radioLang" class="head-right" size="small"> \
              <vue-radio-button label="1">{{$t(\'main.radiolabel1\')}}</vue-radio-button> \
              <vue-radio-button label="2">{{$t(\'main.radiolabel2\')}}</vue-radio-button> \
              <vue-radio-button label="3">{{$t(\'main.radiolabel3\')}}</vue-radio-button> \
            </vue-radio-group> \
            <div class="head-right padding-right20">Version： \
              <vue-button type="text" @click="showChangeLog">{{version}}</vue-button> \
            </div> \
          </vue-col> \
          <vue-col :span="2"> \
            <div class="head-right">\
            <vue-button type="text" @click="toGitLab" style="color: #fff;">GitLab</vue-button><i class="vue-icon-external" style="padding: 7px;"/>\
            </div>\
          </vue-col> \
        </vue-row> \
      </vue-menu> \
      <vue-row class="main-container"> \
        <vue-col :lg=\'5\' :md=\'5\' :sm=\'10\' :xs=\'12\' :class="[\'left-container\',leftClass]"> \
          <vue-input class="filter-input" ref="input" icon="vue-icon-filter-o" v-model="filter" @input="filterHandle"></vue-input> \
          <vue-scrollbar :height="menuHeight" class="menu-container"> \
            <vue-menu router class="container-color" :default-active="defaultActive" :default-openeds="defaultOpeneds" \
              @select="selectHandle"> \
              <vue-submenu index="1"><template slot="title">{{$t(\'main.title1\')}}</template> \
                <vue-menu-item-group class="container-color"> \
                  <vue-menu-item v-for="(menu, index) in menuItems1" :key="index" :index="menu.path">{{$t(menu.head.label)}}</vue-menu-item> \
                </vue-menu-item-group> \
              </vue-submenu> \
              <vue-submenu index="2"><template slot="title">{{$t(\'main.title2\')}}</template> \
                <vue-menu-item-group class="container-color"> \
                  <vue-menu-item v-for="(menu, index) in menuItems2" :key="index" :index="menu.path">{{$t(menu.head.label)}}</vue-menu-item> \
                </vue-menu-item-group> \
              </vue-submenu> \
              <vue-submenu index="3"><template slot="title">{{$t(\'main.title5\')}}</template> \
                <vue-menu-item-group class="container-color"> \
                 <vue-menu-item index="@redirect=example/gantt">{{$t(\'main.title6\')}}</vue-menu-item> \
                 <vue-menu-item index="@redirect=example/charts/charts.html">{{$t(\'main.title7\')}}</vue-menu-item> \
                 <vue-menu-item index="@redirect=example/excel">{{$t(\'main.title8\')}}</vue-menu-item> \
                </vue-menu-item-group> \
              </vue-submenu> \
              <vue-menu-item index="/example">{{$t(\'main.title3\')}}</vue-menu-item> \
              <vue-menu-item index="/redmine">{{$t(\'main.title4\')}}</vue-menu-item> \
            </vue-menu> \
          </vue-scrollbar> \
        </vue-col> \
        <vue-col :lg=\'19\' :md=\'19\' class="right-container"> \
            <router-view></router-view> \
        </vue-col> \
      </vue-row> \
    </div>',
    data: function () {
        return {
            defaultActive: null,
            defaultOpeneds: [],
            menuItems1: menuItems1,
            menuItems2: menuItems2,
            leftClass: "",
            openMenuClass: "",
            headText: "Viy UI",
            radioLang: "1",
            version: VueUtil.version,
            filter: null,
            rightHeight: null,
            menuHeight: null
        }
    },
    watch: {
        radioLang: function (val) {
            switch (val) {
                case "1":
                    VueUtil.setLang("zh");
                    break;
                case "2":
                    VueUtil.setLang("ja");
                    break
                case "3":
                    VueUtil.setLang("en");
                    break
            }
        }
    },
    methods: {
        filterHandle: function () {
            var val = this.filter;
            var menuItems = this.$el.querySelectorAll('.menu-container .vue-menu-item');
            if (VueUtil.trim(val) === '') {
                VueUtil.loop(menuItems, function (menuItem) {
                    VueUtil.removeClass(menuItem, 'hidden');
                });
            } else {
                this.defaultOpeneds = ['1', '2'];
                VueUtil.loop(menuItems, function (menuItem) {
                    VueUtil.addClass(menuItem, 'hidden');
                    if (menuItem.innerHTML.toLowerCase().indexOf(val.toLowerCase()) !== -1) {
                        VueUtil.removeClass(menuItem, 'hidden');
                    }
                });
            }
        },
        headClick: function () {
            var self = this;
            self.$router.push('/');
            self.defaultOpeneds = ['1'];
            VueUtil.removeClass(document.querySelector('.left-container .vue-menu-item.is-active'), 'is-active');
            self.$nextTick(function () {
                self.leftClass = "";
                self.openMenuClass = "";
            });
        },
        showChangeLog: function () {
            var self = this;
            self.$router.push('/changeLog');
            self.defaultOpeneds = ['1'];
            VueUtil.removeClass(document.querySelector('.left-container .vue-menu-item.is-active'), 'is-active');
            self.$nextTick(function () {
                self.leftClass = "";
                self.openMenuClass = "";
            });
        },
        clickHandle: function () {
            if (this.leftClass === "show-left") {
                this.leftClass = "";
                this.openMenuClass = "";
            } else {
                this.leftClass = "show-left";
                this.openMenuClass = "menu-opend"
            }
        },
        selectHandle: function (index) {
            if(index.indexOf('@redirect') == 0) {
                document.location.href = index.replace('@redirect=', '');
            }
            if (index === "/example") {
                document.location.href = "example-index.html"
            }
            if (index === "/redmine") {
              document.location.href = " https://spring21cn.github.io/vue-component/issues/new"
            }
            var self = this;
            self.$nextTick(function () {
                self.leftClass = "";
                self.openMenuClass = "";
            });
        },
        toGitLab: function () {
          window.open(" https://spring21cn.github.io/vue-component/");
        }
    },
    mounted: function () {
        var self = this;
        var lan = VueUtil.getSystemInfo().language.toLowerCase();
        self.radioLang = "1";
        if (lan.indexOf('ja') !== -1) {
            self.radioLang = "2";
        } else if (lan.indexOf('en') !== -1) {
            self.radioLang = "3";
        }
        var inputHeight = self.$refs.input.$el.clientHeight;
        VueUtil.addResizeListener(function () {
            var containerHeight = document.body.clientHeight - self.$el.querySelector('.head-container').clientHeight;
            self.$el.querySelector('.main-container').style.height = containerHeight + 'px';
            self.rightHeight = containerHeight;
            self.menuHeight = containerHeight - inputHeight;
        });
        VueUtil.on(document.querySelector('.right-container'), 'click', function () {
            self.$nextTick(function () {
                self.leftClass = "";
                self.openMenuClass = "";
            });
        });
        self.$nextTick(function () {
            var urlAry = document.location.href.split('#/');
            if (VueUtil.trim(urlAry[1]) !== '') {
                self.defaultActive = "/" + urlAry[1];
            } else {
                self.defaultOpeneds = ['1'];
            }
            var containerHeight = document.body.clientHeight - self.$el.querySelector('.head-container').clientHeight;
            self.$el.querySelector('.main-container').style.height = containerHeight + 'px';
            self.rightHeight = containerHeight;
            self.menuHeight = containerHeight - inputHeight;
            setTimeout(function () {
                if (document.querySelector('.left-container .vue-menu-item.is-active')) {
                    document.querySelector('.left-container').scrollTop = document.querySelector('.left-container .vue-menu-item.is-active').offsetTop;
                }
            }, 500);
        });
    },
    router: DemoRouter
}).$mount('#main');
