'use strict';
var adaptiveGroup = {
  path: '/adaptiveGroup',
  name: 'adaptiveGroup',
  head: {
    label: 'adaptiveGroup.label',
    description: 'adaptiveGroup.description'
  },
  samples: [{
    id: 'adaptiveGroup1',
    label: 'adaptiveGroup.samples1.label',
    description: 'adaptiveGroup.samples1.description',
    template: '<div class="source"><vue-adaptive-group size="sm" custom-class="more" li-class="custom"><vue-button>主按钮</vue-button><vue-button slot="li1">按钮1</vue-button><vue-button slot="li2">按钮2</vue-button><vue-button slot="li3">按钮3</vue-button></vue-adaptive-group></div>',
    code: '<vue-adaptive-group size="sm" custom-class="more" li-class="custom">\n' +
      '  <vue-button>主按钮</vue-button>\n' +
      '  <vue-button slot="li1">按钮1</vue-button>\n' +
      '  <vue-button slot="li2">按钮1</vue-button>\n' +
      '  <vue-button slot="li3">按钮1</vue-button>\n' +
      '</vue-adaptive-group>'
  }, {
    id: 'adaptiveGroup2',
    label: 'Attributes',
    template: '<vue-table class="api" :data="tableData" style="width: 100%"><vue-table-column type="expand"><template slot-scope="scope"><span>{{$t(scope.row.column2)}}</span><br v-if="scope.row.column5"><br v-if="scope.row.column5"><span v-if="scope.row.column5">{{$t(\'main.table.defaultValue\')}}: {{$t(scope.row.column5)}}</span><br v-if="scope.row.column4"><br v-if="scope.row.column4"><span v-if="scope.row.column4">{{$t(\'main.table.acceptedValues\')}}: {{$t(scope.row.column4)}}</span></template></vue-table-column><vue-table-column prop="column1" :label="$t(\'main.table.parameters\')" header-align="left"><template slot-scope="scope">{{$t(scope.row.column1)}}</template></vue-table-column><vue-table-column prop="column3" :label="$t(\'main.table.type\')" header-align="left"><template slot-scope="scope">{{$t(scope.row.column3)}}</template></vue-table-column></vue-table>',
    parameter: {
      data: function () {
        return {
          tableData: [{
            column1: "size",
            column2: "adaptiveGroup.samples2.row1column2",
            column3: "String",
            column4: "lg(1200px), md(992px), sm(768px)",
            column5: "md"
          }, {
            column1: "custom-class",
            column2: "adaptiveGroup.samples2.row2column2",
            column3: "String",
            column4: "",
            column5: ""
          }, {
            column1: "li-class",
            column2: "adaptiveGroup.samples2.row3column2",
            column3: "String",
            column4: "",
            column5: ""
          }, {
            column1: "icon-class",
            column2: "adaptiveGroup.samples2.row4column2",
            column3: "String",
            column4: "",
            column5: "vue-icon-more"
          }]
        }
      }
    },
    notshowmeta: true
  }, {
    id: 'adaptiveGroup3',
    label: 'Slots',
    template: '<vue-table class="api" :data="tableData" style="width: 100%"><vue-table-column prop="column1" :label="$t(\'main.table.name\')" header-align="left" width="150"><template slot-scope="scope">{{$t(scope.row.column1)}}</template></vue-table-column><vue-table-column prop="column2" :label="$t(\'main.table.description\')" header-align="left"><template slot-scope="scope">{{$t(scope.row.column2)}}</template></vue-table-column></vue-table>',
    parameter: {
      data: function () {
        return {
          tableData: [{
            column1: "",
            column2: "adaptiveGroup.samples3.row1column2"
          }, {
            column1: "li+(index)",
            column2: "adaptiveGroup.samples3.row2column2"
          }]
        }
      }
    },
    notshowmeta: true
  }]
};
module.exports = adaptiveGroup;
